import { Component, Input, OnChanges, OnInit } from '@angular/core';
import { Changes } from 'ngx-reactivetoolkit';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';
import { Converter } from 'showdown';

@Component({
  selector: 'sof-markdown',
  template: ` <div [innerHTML]="convertedValue$ | async"></div> `
})
export class MarkdownComponent implements OnInit, OnChanges {
  @Input() value: string;

  @Changes('value') valueChanges$: Observable<string>;

  private converter = new Converter();
  convertedValue$: Observable<string>;

  ngOnChanges(): void {}

  ngOnInit(): void {
    this.convertedValue$ = this.valueChanges$.pipe(
      map(value => this.converter.makeHtml(value))
    );
  }
}
